import React from 'react';
import './Cards.css';
import { Card,CardContent,Typography,Grid} from '@material-ui/core';
import CountUp from 'react-countup';
import Image from '../../images/IMG2.jpg'

const Cards = ({ data:{ confirmed,recovered,deaths,lastUpdate}}) => {
    
    if (!confirmed) {
        return 'loading...';
    }
    return (
        <div className='app__card'>
           <Grid container spacing={3} justify='center'>
 

                <Grid item component={ Card } xs={12} md={3} className='app__infected card' >
                    <CardContent>
                      <Typography color='textSecondary'  gutterBottom>Infected</Typography>
                      <Typography variant='h5'>
                        <CountUp
                          start={0}
                          end={confirmed.value}
                          duration={2.5}
                          separator=','
                        />
                      </Typography>
                      <Typography color='textSecondary' >{new Date(lastUpdate).toDateString()}</Typography>
                      <Typography variant='body2'>Number of active cases of COVID-19<span>üò¢</span></Typography>
                      </CardContent>
                </Grid>
                <Grid item component={ Card } xs={12} md={3} className='app__recovered card' >
                    <CardContent>
                    <Typography color='textSecondary'  gutterBottom>Recovered</Typography>
                    <Typography variant='h5'>
                      <CountUp
                        start={0}
                        end={recovered.value}
                        duration={2.5}
                        separator=','
                      />
                    </Typography>
                    <Typography color='textSecondary' >{new Date(lastUpdate).toDateString()}</Typography>
                    <Typography variant='body2'>Number of recoveries from COVID-19</Typography>
                    </CardContent>
                 </Grid>
                 <Grid item component={ Card } xs={12} md={3} className='app__deaths card' >
                <CardContent>
                    <Typography color='textSecondary'  gutterBottom>Deaths</Typography>
                    <Typography variant='h5'>
                    <CountUp
                        start={0}
                        end={deaths.value}
                        duration={2.5}
                        separator=','
                    />
                    </Typography>
                    <Typography color='textSecondary' >{new Date(lastUpdate).toDateString()}</Typography>
                    <Typography variant='body2'>Number of deaths caused by COVID-19<span>üò≠ </span></Typography>
                   </CardContent>
                </Grid>
           </Grid> 
        </div>
    )
}

export default Cards
